"use client";
import React from "react";

export type SidebarLLM = { id: string; model_name: string; nickname?: string | null };

type Props = {
  open: boolean;
  onClose: () => void;
  email: string;
  llms: SidebarLLM[];
  onAdd: () => void;
  onDelete: (id: string) => void;
};

export default function MobileSidebar({ open, onClose, email, llms, onAdd, onDelete }: Props) {
  return (
    <div className={`fixed inset-0 z-50 ${open ? "pointer-events-auto" : "pointer-events-none"}`}
         aria-hidden={!open}
    >
      {/* Backdrop */}
      <div
        className={`absolute inset-0 bg-black/30 transition-opacity duration-200 ${open ? "opacity-100" : "opacity-0"}`}
        onClick={onClose}
      />
      {/* Panel */}
      <div
        className={`absolute inset-y-0 left-0 w-[85%] max-w-[320px] transform bg-white shadow-xl transition-transform duration-200 ${open ? "translate-x-0" : "-translate-x-full"}`}
        role="dialog"
        aria-modal="true"
      >
        <div className="flex items-center justify-between border-b px-4 py-3">
          <div className="font-semibold">Menu</div>
          <button onClick={onClose} className="rounded-md border px-2 py-1 text-sm hover:bg-gray-50">Close</button>
        </div>
        <div className="h-full overflow-y-auto p-4">
          <div className="flex flex-col space-y-4 text-sm text-gray-700">
            <div className="rounded-md bg-gray-50 p-3 shadow-sm">
              <div className="truncate font-medium" title={email}>{email}</div>
            </div>
            <div className="rounded-md bg-gray-50 p-3 shadow-sm">
              <div className="mb-2 text-xs uppercase text-gray-500">Saved LLMs</div>
              <ul className="space-y-1">
                {llms.map((l) => (
                  <li key={l.id} className="flex items-center justify-between gap-2 truncate">
                    <span className="truncate" title={l.model_name}>{l.nickname || l.model_name}</span>
                    <button
                      onClick={() => onDelete(l.id)}
                      className="rounded px-2 py-0.5 text-xs text-red-600 transition-all hover:bg-red-50"
                    >
                      Delete
                    </button>
                  </li>
                ))}
              </ul>
              <button
                onClick={onAdd}
                className="mt-3 inline-flex w-full items-center justify-center rounded-md border px-3 py-1.5 text-sm transition-all hover:bg-gray-100"
              >
                + Add LLM
              </button>
              <div className="mt-3 h-2 w-full rounded bg-gray-100">
                <div className="h-2 w-1/3 rounded bg-gray-300" />
              </div>
              <div className="mt-1 text-xs text-gray-500">Token usage</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
