"use client";
import { useEffect, useMemo, useState } from "react";
import { useAppStore } from "@/store/useAppStore";
import PromptInputBar from "./components/PromptInputBar";
import ModelResponseCard from "./components/ModelResponseCard";
import LLMModal from "./components/LLMModal";
import TopBar from "./components/TopBar";
import MobileSidebar from "./components/MobileSidebar";

type LLM = {
  id: string;
  model_name: string;
  nickname?: string | null;
};

export default function DashboardClient({
  email,
  initialLlms,
}: {
  email: string;
  initialLlms: LLM[];
}) {
  const { llms, setLLMs, responses, setResponse } = useAppStore();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [prompt, setPrompt] = useState("");
  const [sending, setSending] = useState(false);
  const [navOpen, setNavOpen] = useState(false);
  const [attachedImage, setAttachedImage] = useState<string | null>(null);

  // initialize from server
  useEffect(() => {
    if (initialLlms?.length) setLLMs(initialLlms as any);
  }, [initialLlms, setLLMs]);

  async function refreshLLMs() {
    const res = await fetch("/api/llms");
    if (res.ok) {
      const json = await res.json();
      setLLMs(json.llms);
    }
  }

  async function addLLM(form: { model_name: string; api_key: string; nickname: string }) {
    setSaving(true);
    setError(null);
    const res = await fetch("/api/llms", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(form),
    });
    setSaving(false);
    if (!res.ok) {
      const t = await res.text();
      setError(t);
      return;
    }
    setIsModalOpen(false);
    refreshLLMs();
  }

  async function removeLLM(id: string) {
    const prev = [...llms];
    // optimistic update
    setLLMs(llms.filter((l) => l.id !== id) as any);
    try {
      const res = await fetch(`/api/llms/${id}`, { method: "DELETE" });
      if (!res.ok) {
        // rollback on failure
        setLLMs(prev as any);
        const msg = await res.text();
        console.error("Failed to delete LLM:", msg);
        // best-effort refresh
        refreshLLMs();
      } else {
        // ensure in sync
        refreshLLMs();
      }
    } catch (e: any) {
      setLLMs(prev as any);
      console.error("Delete error:", e);
      refreshLLMs();
    }
  }

  const orderedResponses = useMemo(() => {
    return (llms || []).map((l) => responses[l.id]).filter(Boolean);
  }, [llms, responses]);

  async function sendPrompt() {
    if (!prompt.trim()) return;
    setSending(true);
    // set all to loading
    for (const l of llms) {
      setResponse(l.id, { id: l.id, model_name: l.model_name, nickname: l.nickname, status: "loading", content: undefined, error: undefined });
    }
    try {
      const res = await fetch("/api/compare", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ prompt, image: attachedImage }),
      });
      if (!res.ok) throw new Error(await res.text());
      const json = await res.json();
      for (const r of json.results as any[]) {
        if (r.status === "success") {
          setResponse(r.id, { id: r.id, model_name: r.model_name, nickname: r.nickname, status: "success", content: r.content });
        } else {
          setResponse(r.id, { id: r.id, status: "error", error: r.error });
        }
      }
    } catch (e: any) {
      for (const l of llms) setResponse(l.id, { id: l.id, status: "error", error: String(e.message || e) });
    } finally {
      setSending(false);
    }
  }

  return (
    <div className="min-h-dvh">
      <TopBar onMenuClick={() => setNavOpen(true)} />
      <MobileSidebar
        open={navOpen}
        onClose={() => setNavOpen(false)}
        email={email}
        llms={llms}
        onAdd={() => {
          setNavOpen(false);
          setIsModalOpen(true);
        }}
        onDelete={removeLLM}
      />

      <div className="flex min-h-dvh flex-col">
        <main className="flex-1 p-4">
          <div className="mx-auto max-w-5xl">
            <div className="flex gap-4 overflow-x-auto pb-24">
              {orderedResponses.length ? (
                orderedResponses.map((r) => (
                  <ModelResponseCard
                    key={r.id}
                    title={r.nickname || r.model_name || "Model"}
                    status={r.status}
                    content={r.content}
                    error={r.error}
                  />
                ))
              ) : (
                <div className="min-w-[320px] max-w-[420px] rounded-xl border p-4 text-sm text-gray-400">Run a prompt to see responses.</div>
              )}
            </div>
          </div>
        </main>
        <PromptInputBar
          prompt={prompt}
          onChange={setPrompt}
          onSubmit={sendPrompt}
          disabled={sending || !llms?.length}
          onAttach={(dataUrl) => setAttachedImage(dataUrl)}
        />
      </div>

      <LLMModal open={isModalOpen} onClose={() => setIsModalOpen(false)} onSave={addLLM} saving={saving} error={error} />
    </div>
  );
}
